:root
{
    --mis-color-primary: #007bff;
    --pst-color-on-background: #fff;
    --pst-color-shadow: #d8d8d8;
    --pst-icon-info-circle: "\f05a";
    --pst-color-info: #459db9;
    --bs-danger-rgb: 220, 53, 69;
}

body
{
    margin: 0;
    margin-right: 150px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 175%;
}

code
{
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.ant-table-row
{
    font-size: 14px;
}

.ant-table-tbody
{
    padding: 0 !important;
}

.ant-table-tbody > tr > td
{
    padding: 8px !important;
}

.ant-table-thead
{
    padding: 0 !important;
}

.ant-table-thead > tr > th
{
    padding: 8px !important;
}

.ant-card .ant-card-body
{
    padding: 5px;
}

.admonition
{
    margin: 1.5625em auto;
    padding: 0 .6rem .8rem;
    overflow: hidden;
    page-break-inside: avoid;
    border-left: .2rem solid;
    border-left-color: var(--mis-color-primary);
    border-bottom-color: var(--mis-color-primary);
    border-right-color: var(--mis-color-primary);
    border-top-color: var(--mis-color-primary);
    border-radius: .2rem;
    background-color: var(--pst-color-on-background);
    box-shadow: 0 .2rem .5rem var(--pst-color-shadow), 0 0 .0625rem var(--pst-color-shadow) !important;
}

.note
{
    border-color: var(--mis-color-primary);
}

.admonition-title
{
    margin: 0 -.6rem;
    padding: .4rem .6rem .4rem 0.5rem;
    font-weight: 600;
    position: relative;

}


.admonition-title::before
{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background-color: var(--mis-color-primary);
    opacity: .1;
}


.admonition-title::after
{
    position: absolute;
    left: .5rem;
    width: 1rem;
    height: 1rem;
    /*color: var(--mis-color-primary);*/
    /*font-family: 'Font Awesome 6 Free';*/
    /*font-weight: 900;*/
    /*content: "\f05a";*/
    opacity: 1;
    box-sizing: border-box;
}

.text-danger
{
    --bs-text-opacity: 1;
    color: rgba(var(--bs-danger-rgb), var(--bs-text-opacity)) !important;
}

.fw-bold
{
    font-weight: 700 !important;
}

.ant-anchor-wrapper .ant-anchor .ant-anchor-link
{
    padding-block: 0px;
    padding-inline: 16px 0;
}

